const state = {
    token: null,
    openId: null
}

const getters = {
    token: (state, getters) =>{
        return state.token
    },
    openId: (state, getters) =>{
        return state.openId
    }
}

const actions = {
    storeToken({commit, state}, token){
        commit('storeToken', token)
    },
    storeOpenId({commit, state}, openId){
        commit('storeOpenId', openId)
    }
}

const mutations = {
    storeToken(state, token){
        state.token = token
    },
    storeOpenId(state, openId){
        state.openId = openId
    }
}

export default {
    namespaced: true,
    state,
    getters,
    actions,
    mutations
}


